import React, { useState, useEffect, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { useCart } from "../contexts/CartContext";
import { useAddress } from "../contexts/AddressContext";
import BackHeader from "../components/BackHeader";
import NavigationBar from "../components/NavigationBar";
import EmptyState from "../components/EmptyState";
import ImageWithFallback from "../components/ImageWithFallback";
import {
  createCart,
  getCart,
  transformCartToRequest,
} from "../api/cartService";
import { getRestaurantDetails } from "../api/restaurantService";
import useTranslation from "../hooks/useTranslation";
import {
  SupportedCurrency,
  formatPrice as formatPriceWithCurrency,
  getCurrencyFromLanguage,
  getPriceFieldByCurrency
} from "../utils/currencyUtils";
import { getCurrentLanguage, getLocalizedValue } from "../config/languageConfig";

const CartPage: React.FC = () => {
  const navigate = useNavigate();
  const { address } = useAddress();
  const {
    cart,
    removeItem,
    getTotalPrice,
    updateItemQuantity,
    getMinOrderAmount,
    isDeliveryAvailable,
    getAmountToMinOrder,
    updateCartPricesForLanguage,
    setRestaurantMinOrderAmount,
  } = useCart();
  const { t } = useTranslation();
  const [currentLang, setCurrentLang] = useState(getCurrentLanguage());
  
  // 로딩 상태 추가
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [errorMsg, setErrorMsg] = useState<string | null>(null);

  // 현재 언어에 맞는 통화 설정
  const [selectedCurrency, setSelectedCurrency] = useState<SupportedCurrency>(
    getCurrencyFromLanguage(currentLang)
  );
  
  // 가게 정보 조회 및 최소 주문 금액 설정
  useEffect(() => {
    const fetchRestaurantDetails = async () => {
      // 장바구니가 비어있거나 레스토랑 ID가 없으면 실행하지 않음
      if (!cart || !cart.restaurantId) return;
      
      // 이미 최소 주문 금액이 설정되어 있으면 실행하지 않음
      const currentMinOrderAmount = getMinOrderAmount();
      if (currentMinOrderAmount !== null) return;
      
      try {
        console.log(`[CartPage] 가게 정보 조회 중: ${cart.restaurantId}`);
        const restaurant = await getRestaurantDetails(String(cart.restaurantId));
        
        if (restaurant) {
          // 현재 언어에 맞는 통화 및 최소 주문 금액 필드 결정
          const currency = getCurrencyFromLanguage(currentLang);
          const currencyCode = currency.charAt(0) + currency.slice(1).toLowerCase();
          const minOrderAmountField = `minOrderAmount${currencyCode}` as keyof typeof restaurant;
          
          // 언어에 맞는 최소 주문금액이 있으면 사용, 없으면 기본 최소 주문금액 사용
          const minAmount = (restaurant[minOrderAmountField] as number) || restaurant.minOrderAmount;
          
          console.log(`[CartPage] 가게 정보에서 최소 주문 금액 설정: ${minAmount} ${currency}`);
          setRestaurantMinOrderAmount(minAmount, currency);
        }
      } catch (error) {
        console.error('[CartPage] 가게 정보 조회 실패:', error);
      }
    };
    
    fetchRestaurantDetails();
  }, [cart, currentLang, getMinOrderAmount, setRestaurantMinOrderAmount]);
  
  // 현재 설정된 최소 주문 금액 로깅
  useEffect(() => {
    console.log(`[CartPage] 현재 설정된 최소 주문 금액: ${getMinOrderAmount()}, 통화: ${selectedCurrency}`);
  }, [getMinOrderAmount, selectedCurrency]);
  
  // 언어 변경 감지
  useEffect(() => {
    const handleLanguageChange = () => {
      const newLang = getCurrentLanguage();
      setCurrentLang(newLang);
      setSelectedCurrency(getCurrencyFromLanguage(newLang));
      
      // CartContext의 가격 업데이트 함수 호출
      updateCartPricesForLanguage?.(newLang);
      
      // 필요한 경우 forceUpdate 생성 (리렌더링 강제)
      // 이는 특히 최소 주문 금액 상태를 업데이트하고 
      // 이미 계산된 값들을 재계산하도록 함
      setForceUpdate(prev => !prev);
    };
    
    window.addEventListener('language-changed', handleLanguageChange);
    return () => {
      window.removeEventListener('language-changed', handleLanguageChange);
    };
  }, []);
  
  // forceUpdate 상태 추가
  const [forceUpdate, setForceUpdate] = useState<boolean>(false);

  // 배송비 및 수수료 (하드코딩된 값)
  // 각 통화별 기본 값 설정
  const getDeliveryFee = (): number => {
    switch(selectedCurrency) {
      case 'USD': return 4.5; // 약 3달러
      case 'JPY': return 519; // 약 400엔
      case 'CNY': return 26; // 약 20위안
      case 'TWD': return 107; // 약 100대만달러
      case 'KRW':
      default: return 3000; // 3000원
    }
  };

  const getProxyFee = (): number => {
    switch(selectedCurrency) {
      case 'USD': return 2; // 약 1달러
      case 'JPY': return 208; // 약 150엔
      case 'CNY': return 11; // 약 7위안
      case 'TWD': return 43; // 약 30대만달러
      case 'KRW':
      default: return 1000; // 1000원
    }
  };

  // 소계 및 총 주문 금액 계산 - selectedCurrency가 변경될 때마다 다시 계산
  const subtotal = useMemo(() => getTotalPrice(), [getTotalPrice, selectedCurrency, cart]);
  const DELIVERY_FEE = useMemo(() => getDeliveryFee(), [selectedCurrency]);
  const PROXY_FEE = useMemo(() => getProxyFee(), [selectedCurrency]);
  const total = useMemo(() => subtotal + DELIVERY_FEE + PROXY_FEE, [subtotal, DELIVERY_FEE, PROXY_FEE]);
  
  // 최소 주문 금액 (CartContext에서 제공)
  const minOrderAmount = useMemo(() => {
    const amount = getMinOrderAmount();
    console.log(`최소 주문 금액 계산: ${amount}, 통화: ${selectedCurrency}`);
    return amount;
  }, [getMinOrderAmount, selectedCurrency, forceUpdate, cart]);

  // 최소 주문 금액 충족 여부
  const deliveryAvailable = useMemo(() => {
    const result = isDeliveryAvailable(minOrderAmount);
    console.log(`배달 가능 여부: ${result}, 최소주문금액: ${minOrderAmount}, 통화: ${selectedCurrency}, 총 금액: ${subtotal}`);
    return result;
  }, [isDeliveryAvailable, minOrderAmount, selectedCurrency, forceUpdate, subtotal]);
  
  // 디버깅을 위한 효과
  useEffect(() => {
    if (minOrderAmount) {
      console.log(`최소 주문 금액 정보: ${minOrderAmount}, 통화: ${selectedCurrency}, 버튼 비활성화: ${!deliveryAvailable}, 총 금액: ${subtotal}`);
    }
  }, [deliveryAvailable, minOrderAmount, selectedCurrency, subtotal]);
  
  const amountToMinOrder = useMemo(() => getAmountToMinOrder(minOrderAmount), [getAmountToMinOrder, minOrderAmount, selectedCurrency, forceUpdate, subtotal]);

  // 가격 포맷팅 및 현재 통화에 맞는 가격 반환
  const formatPrice = (price: number) => {
    return formatPriceWithCurrency(price, selectedCurrency);
  };
  
  // 현재 통화에 맞는 아이템 가격 반환
  const getItemPrice = (item: any) => {
    const priceField = getPriceFieldByCurrency(selectedCurrency);
    return item[priceField] !== undefined ? item[priceField] : item.price;
  };

  // 가게 상세 페이지로 이동
  const navigateToRestaurant = () => {
    if (cart && cart.restaurantId) {
      console.log(`CartPage: navigating to restaurant ${cart.restaurantId}`);
      navigate(`/restaurant/${cart.restaurantId}`, { replace: false });
    }
  };

  // 레스토랑 브라우징 페이지로 이동
  const navigateToRestaurants = () => {
    console.log('CartPage: navigating to restaurants list');
    navigate("/restaurants", { replace: false }); // 음식점 목록 페이지로 이동
  };

  // 수량 증가
  const increaseQuantity = (itemId: string, currentQuantity: number) => {
    updateItemQuantity(itemId, currentQuantity + 1);
  };

  // 수량 감소
  const decreaseQuantity = (itemId: string, currentQuantity: number) => {
    if (currentQuantity > 1) {
      updateItemQuantity(itemId, currentQuantity - 1);
    } else {
      removeItem(itemId);
    }
  };

  // 메뉴 삭제
  const handleRemoveItem = (itemId: string) => {
    removeItem(itemId);
  };

  // 체크아웃 진행
  const handleCheckout = async () => {
    if (!cart) return;

    // 최소 주문 금액이 충족되지 않았을 때 알림 표시
    if (!deliveryAvailable) {
      // 경고 메시지 표시
      setErrorMsg(t("cart.minOrderWarning"));
      
      // 3초 후 메시지 자동 삭제
      setTimeout(() => {
        setErrorMsg(null);
      }, 3000);
      
      return; // 여기서 함수 종료됨
    }

    try {
      // 로딩 상태 설정
      setIsLoading(true);
      setErrorMsg(null);

      // 주소 ID 가져오기
      const addressId = address?.addressId || ""; // 저장된 주소 ID 또는 임시 ID 사용

      if (!address?.isComplete) {
        setErrorMsg(t("cart.setAddressFirst"));
        setIsLoading(false);
        navigate("/address-setup");
        return;
      }

      if (!addressId) {
        console.warn("Address ID is missing. Using temporary ID.");
      }

      // 1. 장바구니 생성 요청
      const cartRequest = transformCartToRequest(cart, addressId);
      const cartResponse = await createCart(cartRequest);

      if (!cartResponse || !cartResponse.id) {
        throw new Error(t("cart.failedToCreateCart"));
      }

      console.log("Cart created successfully:", cartResponse);

      // 2. 생성된 장바구니 정보로 주문 ID 조회
      const cartId = cartResponse.id;
      const cartDetail = await getCart(cartId);

      if (!cartDetail || !cartDetail.orderId) {
        throw new Error(t("cart.failedToGetOrderId"));
      }

      console.log("Order ID retrieved successfully:", cartDetail.orderId);

      // 주문 ID를 사용하여 결제 페이지로 이동 (pass selected currency)
      navigate(
        `/checkout?orderId=${cartDetail.orderId}&currency=${selectedCurrency}`,
      );
    } catch (error) {
      // 에러 처리
      console.error("Checkout error:", error);
      setErrorMsg(
        error instanceof Error ? error.message : t("cart.checkoutFailed"),
      );
    } finally {
      setIsLoading(false);
    }
  };

  // 카트가 비어있을 때
  if (!cart || cart.items.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50">
        <BackHeader title={t("nav.cart")} />
        <div className="flex flex-col items-center justify-center h-[calc(100vh-120px)] px-6 text-center">
          <h2 className="text-2xl font-bold text-gray-800 mb-3">
            {t("cart.empty")}
          </h2>
          <p className="text-gray-500 mb-8">{t("cart.addItems")}</p>
          <button
            onClick={navigateToRestaurants}
            className="bg-red-500 text-white py-3 px-6 rounded-md font-medium hover:bg-red-600 transition-colors"
          >
            {t("cart.browseRestaurants")}
          </button>
        </div>
        <NavigationBar />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* GNB */}
      <BackHeader title={t("nav.cart")} />

      <main className="px-4 pb-[128px]">
        {/* 가게 정보 */}
        <div
          className="py-4 bg-white mb-4 border-b border-gray-200 cursor-pointer -mx-4 px-4"
          onClick={navigateToRestaurant}
        >
          <h2 className="text-lg font-medium text-left flex items-center text-gray-800">
            {/* 
              현재 언어에 맞는 가게 이름 표시, 없으면 영어 이름, 그것도 없으면 기본 이름 사용
            */}
            {(() => {
              // 이름 표시 형식 (말줄임표 처리)
              const formatName = (name: string): string => {
                if (!name || typeof name !== 'string') return 'Unknown Restaurant';
                return name.length > 20 ? `${name.substring(0, 18)}...` : name;
              };
              
              // 1. 현재 언어에 맞는 이름 필드 선택
              let displayName: string | undefined;
              
              // 언어별 필드 이름
              if (currentLang === 'en' && cart.restaurantNameEn) {
                displayName = cart.restaurantNameEn;
              } else if (currentLang === 'ja' && cart.restaurantNameJa) {
                displayName = cart.restaurantNameJa;
              } else if (currentLang === 'zh-CN' && cart.restaurantNameZhCn) {
                displayName = cart.restaurantNameZhCn;
              } else if (currentLang === 'zh-TW' && cart.restaurantNameZhTw) {
                displayName = cart.restaurantNameZhTw;
              } else if (currentLang === 'ko' && cart.restaurantNameKo) {
                displayName = cart.restaurantNameKo;
              }
              
              // 현재 언어에 맞는 이름이 없으면 영어 이름, 그것도 없으면 기본 이름 사용
              return formatName(displayName || cart.restaurantNameEn || cart.restaurantName);
            })()}
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              fill="currentColor"
              className="w-5 h-5 ml-1 text-gray-500"
            >
              <path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z" />
            </svg>
          </h2>
        </div>

        {/* 카트 아이템 목록 */}
        <div className="divide-y divide-gray-200">
          {cart.items.map((item) => (
            <div key={item.id} className="py-4 flex items-start">
              {/* 메뉴 이미지 */}
              <div className="w-20 h-20 rounded-md overflow-hidden flex-shrink-0 bg-gray-100">
                <ImageWithFallback
                  src={item.image || ''}
                  alt={item.name}
                  className="w-full h-full object-cover"
                  fallback="https://source.unsplash.com/random/100x100/?food"
                />
              </div>

              {/* 메뉴 정보 */}
              <div className="ml-3 flex-grow min-w-0">
                <div className="flex justify-between items-start">
                  <h3 className="text-lg font-medium text-gray-900 truncate pr-2">
                    {/* 다국어 지원을 위해 getLocalizedValue 사용 */}
                    {(() => {
                      // 현재 언어에 맞는 이름 필드가 있다면 사용, 없으면 기본 이름 사용
                      const nameField = `name${currentLang === 'en' ? 'En' : 
                                      currentLang === 'ja' ? 'Ja' : 
                                      currentLang === 'zh-CN' ? 'ZhCn' : 
                                      currentLang === 'zh-TW' ? 'ZhTw' : ''}` as keyof typeof item;
                      
                      // 이름 필드가 있으면 사용, 없으면 기본 이름 사용
                      return item[nameField] !== undefined 
                        ? String(item[nameField]) 
                        : getLocalizedValue(item.name, {
                            en: item.nameEn as string, 
                            ja: item.nameJa as string,
                            'zh-CN': item.nameZhCn as string,
                            'zh-TW': item.nameZhTw as string
                          });
                    })()}
                  </h3>
                  <button
                    type="button"
                    className="text-gray-400 hover:text-red-500 flex-shrink-0 ml-1"
                    onClick={() => handleRemoveItem(item.id)}
                    aria-label={`${t('cart.remove')} ${item.name}`}
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                    </svg>
                  </button>
                </div>

                {/* 가격과 수량 컨트롤 */}
                <div className="flex items-center justify-between mt-2">
                  {/* 가격 표시 - 좌측 */}
                  <div className="text-gray-600 text-base">
                    {formatPrice(getItemPrice(item))}
                    <span className="text-xs ml-1">{t('cart.perPiece')}</span>
                  </div>
                  
                  {/* 수량 컨트롤 - 우측 */}
                  <div className="flex items-center">
                    <button
                      type="button"
                      className="w-8 h-8 flex items-center justify-center border rounded-md border-gray-300 text-gray-700"
                      onClick={() => decreaseQuantity(item.id, item.quantity)}
                      aria-label={`${t('cart.decrease')} ${item.name}`}
                      disabled={item.quantity <= 1}
                    >
                      <span className="text-lg font-medium">−</span>
                    </button>
                    <span className="mx-4 text-base font-medium">{item.quantity}</span>
                    <button
                      type="button"
                      className="w-8 h-8 flex items-center justify-center border rounded-md bg-red-500 text-white border-red-500"
                      onClick={() => increaseQuantity(item.id, item.quantity)}
                      aria-label={`${t('cart.increase')} ${item.name}`}
                    >
                      <span className="text-lg font-medium">+</span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* 가격 요약 */}
        <div className="bg-white p-4 rounded-lg shadow-sm mb-4">
          <h3 className="text-lg font-medium mb-3 text-gray-800 text-left">
            {t("cart.orderSummary")}
          </h3>
          <div className="space-y-2 text-left">
            <div className="flex justify-between">
              <span className="text-gray-600">{t("cart.subtotal")}</span>
              <span className="font-medium">{formatPrice(subtotal)}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">{t("cart.deliveryFee")}</span>
              <span className="font-medium">{formatPrice(DELIVERY_FEE)}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">{t("cart.proxyFee")}</span>
              <span className="font-medium">{formatPrice(PROXY_FEE)}</span>
            </div>
            <div className="border-t pt-2 mt-2 flex justify-between">
              <span className="font-medium text-gray-800">
                {t("cart.total")}
              </span>
              <span className="font-bold text-xl">{formatPrice(total)}</span>
            </div>
          </div>
        </div>

        {/* 배달 최소 주문 금액 안내 (비활성화 상태일 때만) */}
        {!deliveryAvailable && minOrderAmount && (
          <div className="bg-yellow-50 p-4 rounded-lg border border-yellow-200 mb-4 text-left">
            <div className="flex items-start">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 text-yellow-500 mt-0.5 mr-2 flex-shrink-0"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
                  clipRule="evenodd"
                />
              </svg>
              <div>
                <p className="text-yellow-700 font-medium">
                  {t("cart.minOrderWarning")}
                </p>
                <p className="text-yellow-600 text-sm mt-1">
                  {t("cart.needMore", { amount: formatPrice(amountToMinOrder) })}
                </p>
              </div>
            </div>
          </div>
        )}

        {/* 에러 메시지 */}
        {errorMsg && (
          <div className="bg-red-50 p-4 rounded-lg border border-red-200 mb-4 text-left">
            <div className="flex items-start">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 text-red-500 mt-0.5 mr-2 flex-shrink-0"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
                  clipRule="evenodd"
                />
              </svg>
              <p className="text-red-700">{errorMsg}</p>
            </div>
          </div>
        )}
      </main>

      {/* 결제 버튼 영역 */}
      <div className="fixed bottom-[60px] left-0 right-0 bg-white shadow-top p-4 z-20">
        <button
          onClick={handleCheckout}
          disabled={isLoading || !deliveryAvailable}
          className={`w-full py-3 px-4 rounded-md font-semibold text-white flex justify-center items-center ${
            !isLoading && deliveryAvailable
              ? "bg-red-500 hover:bg-red-600"
              : !isLoading && !deliveryAvailable
              ? "bg-yellow-500 hover:bg-yellow-600"
              : "bg-gray-400 cursor-not-allowed"
          }`}
        >
          {isLoading ? (
            <>
              <svg
                className="animate-spin -ml-1 mr-2 h-5 w-5 text-white"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
              >
                <circle
                  className="opacity-25"
                  cx="12"
                  cy="12"
                  r="10"
                  stroke="currentColor"
                  strokeWidth="4"
                ></circle>
                <path
                  className="opacity-75"
                  fill="currentColor"
                  d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                ></path>
              </svg>
              {t("cart.processing")}
            </>
          ) : !deliveryAvailable ? (
            t("cart.minOrderWarning")
          ) : (
            t("cart.proceedToCheckout")
          )}
        </button>
      </div>

      {/* 네비게이션 바 */}
      <NavigationBar />
    </div>
  );
};

export default CartPage;