import apiClient from './config';
import { MenuSection, MenuResponse, MenuItem } from '../types/menu';
import { buildQueryString, handleApiError, logApiResponse } from './utils';
import { getCurrentLanguage, SupportedLanguage } from '../config/languageConfig';
import { createCacheKey, getCacheData, setCacheData } from './cacheUtils';
import { getCurrencyFromLanguage, getPriceFieldByCurrency } from '../utils/currencyUtils';
import { getTranslation } from '../config/translations';

/**
 * 가게의 메뉴 목록 조회
 * @param restaurantId 가게 ID
 * @param lang 언어 설정 (기본값: 현재 설정된 언어)
 * @returns 메뉴 섹션 목록
 */
export const getRestaurantMenus = async (
  restaurantId: string | number,
  lang?: string
): Promise<MenuSection[]> => {
  const endpoint = `/api/v1/stores/${restaurantId}/menus`;
  const currentLang = lang || getCurrentLanguage();
  
  try {
    // 쿼리 파라미터와 엔드포인트
    const params = { lang: currentLang };
    
    // 캐시 키 생성
    const cacheKey = createCacheKey(endpoint, params);
    
    // 캐시된 데이터 확인
    const cachedData = getCacheData<MenuSection[]>(cacheKey, currentLang as SupportedLanguage);
    if (cachedData) {
      return cachedData;
    }
    
    // 언어 설정 쿼리 파라미터 추가
    const queryString = buildQueryString(params);
    const response = await apiClient.get(`${endpoint}${queryString}`);
    logApiResponse(endpoint, response.data);
    
    // API 응답을 애플리케이션 데이터 구조로 변환
    const menuData = transformMenuData(response.data, currentLang as SupportedLanguage);
    
    // 변환된 데이터 캐싱
    setCacheData(cacheKey, menuData, currentLang as SupportedLanguage);
    
    return menuData;
  } catch (error) {
    const apiError = handleApiError(error);
    console.error(`Error fetching restaurant menus: ${apiError.message}`, apiError);
    
    // 에러 발생 시 빈 배열 반환
    return [];
  }
};

/**
 * 메뉴 상세 정보 조회
 * @param restaurantId 가게 ID
 * @param menuId 메뉴 ID
 * @param lang 언어 설정 (기본값: 현재 설정된 언어)
 * @returns 메뉴 상세 정보
 */
export const getMenuDetail = async (
  restaurantId: string | number,
  menuId: string | number,
  lang?: string
): Promise<MenuItem | null> => {
  const endpoint = `/api/v1/stores/${restaurantId}/menus/${menuId}`;
  const currentLang = lang || getCurrentLanguage();
  
  try {
    // 쿼리 파라미터와 엔드포인트
    const params = { lang: currentLang };
    
    // 캐시 키 생성
    const cacheKey = createCacheKey(endpoint, params);
    
    // 캐시된 데이터 확인
    const cachedData = getCacheData<MenuItem>(cacheKey, currentLang as SupportedLanguage);
    if (cachedData) {
      return cachedData;
    }
    
    // 언어 설정 쿼리 파라미터 추가
    const queryString = buildQueryString(params);
    const response = await apiClient.get(`${endpoint}${queryString}`);
    logApiResponse(endpoint, response.data);
    
    // API 응답을 애플리케이션 데이터 구조로 변환
    const menuItem = transformMenuItemData(response.data, currentLang as SupportedLanguage);
    
    // 변환된 데이터 캐싱
    setCacheData(cacheKey, menuItem, currentLang as SupportedLanguage);
    
    return menuItem;
  } catch (error) {
    const apiError = handleApiError(error);
    console.error(`Error fetching menu detail: ${apiError.message}`, apiError);
    
    // 에러 발생 시 null 반환
    return null;
  }
};

/**
 * API 응답 데이터를 애플리케이션에서 사용하는 MenuItem 형식으로 변환
 * @param apiMenuItem API에서 반환된 메뉴 아이템 데이터
 * @param lang 언어 설정
 * @returns 변환된 MenuItem 객체
 */
function transformMenuItemData(apiMenuItem: any, lang: SupportedLanguage = 'en'): MenuItem {
  if (!apiMenuItem || typeof apiMenuItem !== 'object') {
    throw new Error('Invalid menu item data');
  }

  // 언어에 맞는 통화 및 가격 필드 결정
  const currency = getCurrencyFromLanguage(lang);
  const priceField = getPriceFieldByCurrency(currency);
  
  // 기본 가격 또는 언어별 가격 사용
  const price = apiMenuItem[priceField] !== undefined ? apiMenuItem[priceField] : apiMenuItem.price || 0;
  
  // 옵션 변환
  const options = Array.isArray(apiMenuItem.options) ? apiMenuItem.options.map((opt: { id: string; name: string; description?: string }) => ({
    id: opt.id,
    name: opt.name,
    slug: opt.id,
    sourceId: 0,
    mandatory: false,
    multiple: false,
    multipleCount: 1,
    isAvailableQuantity: false,
    menuOptionItems: [{
      id: opt.id,
      name: opt.name,
      slug: opt.id,
      sourceId: 0,
      price: 0,
      description: opt.description || '',
      soldout: false,
      isDeposit: false,
      depositPrice: 0,
      depositDescription: ''
    }]
  })) : [];
  
  // 메뉴 아이템 변환
  return {
    id: apiMenuItem.id,
    name: apiMenuItem.name,
    subtitle: "",
    price: price,
    description: apiMenuItem.description || "",
    image: apiMenuItem.image || null,
    originalImage: apiMenuItem.image || null,
    slug: apiMenuItem.id,
    sourceId: 0,
    reviewCount: 0,
    soldout: false,
    oneDish: false,
    menuOptions: options,
    nameEn: apiMenuItem.nameEn || apiMenuItem.name, // 영문 이름이 없는 경우 기본 이름 사용
    descriptionEn: apiMenuItem.descriptionEn || apiMenuItem.description, // 영문 설명이 없는 경우 기본 설명 사용
    // 환율에 따른 가격 정보 추가
    priceKrw: apiMenuItem.priceKrw,
    priceUsd: apiMenuItem.priceUsd,
    priceJpy: apiMenuItem.priceJpy,
    priceCny: apiMenuItem.priceCny,
    priceTwd: apiMenuItem.priceTwd
  };
}

/**
 * API 응답 데이터를 애플리케이션에서 사용하는 MenuSection 형식으로 변환
 * @param apiMenus API에서 반환된 메뉴 데이터
 * @param lang 언어 설정
 * @returns 변환된 MenuSection 배열
 */
function transformMenuData(apiMenus: any[], lang: SupportedLanguage = 'en'): MenuSection[] {
  if (!Array.isArray(apiMenus) || apiMenus.length === 0) {
    return [];
  }
  
  // 언어에 맞는 통화 및 가격 필드 결정
  const currency = getCurrencyFromLanguage(lang);
  const priceField = getPriceFieldByCurrency(currency);
  
  // 메뉴 섹션별로 그룹화
  const menusBySection: Record<string, MenuItem[]> = {};
  
  // API 응답의 각 메뉴 아이템을 변환하여 섹션별로 그룹화
  apiMenus.forEach(item => {
    const sectionName = item.section || 'Other';
    
    if (!menusBySection[sectionName]) {
      menusBySection[sectionName] = [];
    }
    
    // 기본 가격 또는 언어별 가격 사용
    const price = item[priceField] !== undefined ? item[priceField] : item.price || 0;
    
    // 옵션 변환
    const options = Array.isArray(item.options) ? item.options.map((opt: { id: string; name: string; description?: string }) => ({
      id: opt.id,
      name: opt.name,
      slug: opt.id,
      sourceId: 0,
      mandatory: false,
      multiple: false,
      multipleCount: 1,
      isAvailableQuantity: false,
      menuOptionItems: [{
        id: opt.id,
        name: opt.name,
        slug: opt.id,
        sourceId: 0,
        price: 0,
        description: opt.description || '',
        soldout: false,
        isDeposit: false,
        depositPrice: 0,
        depositDescription: ''
      }]
    })) : [];
    
    // 메뉴 아이템 변환
    const menuItem: MenuItem = {
      id: item.id,
      name: item.name,
      subtitle: "",
      price: price,
      description: item.description || "",
      image: item.image || null,
      originalImage: item.image || null,
      slug: item.id,
      sourceId: 0,
      reviewCount: 0,
      soldout: false,
      oneDish: false,
      menuOptions: options,
      nameEn: item.nameEn || item.name, // 영문 이름이 없는 경우 기본 이름 사용
      descriptionEn: item.descriptionEn || item.description, // 영문 설명이 없는 경우 기본 설명 사용
      // 환율에 따른 가격 정보 추가
      priceKrw: item.priceKrw,
      priceUsd: item.priceUsd,
      priceJpy: item.priceJpy,
      priceCny: item.priceCny,
      priceTwd: item.priceTwd
    };
    
    menusBySection[sectionName].push(menuItem);
  });
  
  // 그룹화된 메뉴를 MenuSection 배열로 변환
  let menuSections: MenuSection[] = Object.entries(menusBySection).map(([sectionName, items], index) => ({
    id: `section-${index}`,
    name: sectionName,
    nameEn: sectionName,
    image: null,
    msType: sectionName,
    slug: sectionName.toLowerCase().replace(/\s+/g, '-'),
    sourceId: index,
    menuItems: items
  }));
  
  // 하단에 배치할 섹션 이름들 - 다국어 지원
  const currentLang = lang || getCurrentLanguage();
  
  // 각 섹션의 모든 언어 버전을 포함
  const bottomSectionNames = {
    sideMenu: [
      getTranslation('section.sideMenu', 'en'),
      getTranslation('section.sideMenu', 'ko'),
      getTranslation('section.sideMenu', 'ja'),
      getTranslation('section.sideMenu', 'zh-CN'),
      getTranslation('section.sideMenu', 'zh-TW'),
      getTranslation('section.sideDish', 'en'),
      getTranslation('section.sideDish', 'ko'),
      getTranslation('section.sideDish', 'ja'),
      getTranslation('section.sideDish', 'zh-CN'),
      getTranslation('section.sideDish', 'zh-TW')
    ],
    softDrink: [
      getTranslation('section.softDrink', 'en'),
      getTranslation('section.softDrink', 'ko'),
      getTranslation('section.softDrink', 'ja'),
      getTranslation('section.softDrink', 'zh-CN'),
      getTranslation('section.softDrink', 'zh-TW')
    ],
    drinks: [
      getTranslation('section.drinks', 'en'),
      getTranslation('section.drinks', 'ko'),
      getTranslation('section.drinks', 'ja'),
      getTranslation('section.drinks', 'zh-CN'),
      getTranslation('section.drinks', 'zh-TW')
    ],
    beverages: [
      getTranslation('section.beverages', 'en'),
      getTranslation('section.beverages', 'ko'),
      getTranslation('section.beverages', 'ja'),
      getTranslation('section.beverages', 'zh-CN'),
      getTranslation('section.beverages', 'zh-TW')
    ]
  };
  
  // 모든 하단 섹션 이름을 단일 배열로 평면화
  const bottomSections = [
    ...bottomSectionNames.sideMenu,
    ...bottomSectionNames.softDrink,
    ...bottomSectionNames.drinks,
    ...bottomSectionNames.beverages
  ];
  
  // 섹션 정렬: 하단에 배치할 섹션들을 찾아서 제거한 후 나중에 추가
  const sectionsToMoveToBottom = [];
  
  for (const sectionName of bottomSections) {
    const index = menuSections.findIndex(section => 
      section.name === sectionName || section.nameEn === sectionName
    );
    
    if (index !== -1) {
      const removedSection = menuSections.splice(index, 1)[0];
      sectionsToMoveToBottom.push(removedSection);
    }
  }
  
  // 하단에 배치할 섹션들 추가
  menuSections = [...menuSections, ...sectionsToMoveToBottom];
  
  return menuSections;
} 