/**
 * Currency utilities for handling different currencies in PayPal payments
 */

import { SupportedLanguage } from '../config/languageConfig';

// Define supported currencies
export type SupportedCurrency = "USD" | "JPY" | "CNY" | "TWD" | "KRW";

// Exchange rates to USD (as of latest update)
// These should be updated regularly or fetched from an API in production
export const EXCHANGE_RATES: Record<SupportedCurrency, number> = {
  USD: 1.0, // 1 USD = 1 USD
  KRW: 0.00074, // 1 KRW = 0.00074 USD (약 1350원 = 1 USD)
  JPY: 0.0066, // 1 JPY = 0.0066 USD (약 152엔 = 1 USD)
  CNY: 0.138, // 1 CNY = 0.138 USD (약 7.24위안 = 1 USD)
  TWD: 0.031, // 1 TWD = 0.031 USD (약 32.3 대만 달러 = 1 USD)
};

// Currency symbols
export const CURRENCY_SYMBOLS: Record<SupportedCurrency, string> = {
  USD: "$",
  KRW: "₩",
  JPY: "¥",
  CNY: "¥",
  TWD: "NT$",
};

/**
 * 언어에 따른 기본 통화 매핑
 */
export const LANGUAGE_TO_CURRENCY: Record<SupportedLanguage, SupportedCurrency> = {
  'en': 'USD',
  'ko': 'KRW',
  'ja': 'JPY',
  'zh-CN': 'CNY',
  'zh-TW': 'TWD'
};

/**
 * 언어 코드에 따른 기본 통화 반환
 * @param language - 언어 코드
 * @returns 해당 언어의 기본 통화
 */
export const getCurrencyFromLanguage = (language: SupportedLanguage): SupportedCurrency => {
  return LANGUAGE_TO_CURRENCY[language] || 'USD';
};

/**
 * 통화에 따른 가격 필드 이름 반환
 * @param currency - 통화 코드
 * @returns 해당 통화의 가격 필드 이름
 */
export const getPriceFieldByCurrency = (currency: SupportedCurrency): string => {
  switch (currency) {
    case 'KRW': return 'priceKrw';
    case 'USD': return 'priceUsd';
    case 'JPY': return 'priceJpy';
    case 'CNY': return 'priceCny';
    case 'TWD': return 'priceTwd';
    default: return 'price';
  }
};

/**
 * Format price with currency symbol
 * @param price - Price to format
 * @param currency - Currency code
 * @returns Formatted price with currency symbol
 */
export const formatPrice = (
  price: number,
  currency: SupportedCurrency = "USD",
): string => {
  // 모든 통화에 대해 소수점 제거 (정수로 표시)
  switch (currency) {
    case "USD":
      return `$${Math.round(price)}`;
    case "JPY":
      return `¥${Math.round(price)}`;
    case "CNY":
      return `¥${Math.round(price)}`;
    case "TWD":
      return `NT$${Math.round(price)}`;
    case "KRW":
      return `₩${Math.round(price).toLocaleString()}`;
    default:
      return `${Math.round(price)}`;
  }
};

/**
 * Convert price from source currency to target currency
 * @param price - Price to convert
 * @param sourceCurrency - Source currency code
 * @param targetCurrency - Target currency code
 * @returns Converted price
 */
export const convertCurrency = (
  price: number,
  sourceCurrency: SupportedCurrency,
  targetCurrency: SupportedCurrency,
): number => {
  // If same currency, no conversion needed
  if (sourceCurrency === targetCurrency) {
    return price;
  }
  
  // Convert to USD first, then to target currency
  const priceInUSD = price * EXCHANGE_RATES[sourceCurrency];
  console.log(`[convertCurrency] ${price} ${sourceCurrency} -> ${priceInUSD.toFixed(4)} USD`);

  if (targetCurrency === "USD") {
    return Math.round(priceInUSD * 100) / 100; // USD일 경우 센트 단위로 반올림하여 반환
  }

  const convertedPrice = priceInUSD / EXCHANGE_RATES[targetCurrency];
  console.log(`[convertCurrency] ${priceInUSD.toFixed(4)} USD -> ${convertedPrice.toFixed(2)} ${targetCurrency}`);
  
  // 각 통화에 맞게 반올림 처리
  let roundedPrice: number;
  
  // targetCurrency가 USD일 수 없음 (위에서 이미 처리됨)
  switch(targetCurrency) {
    case 'JPY':
    case 'KRW':
      roundedPrice = Math.round(convertedPrice); // 정수로 반올림
      break;
    case 'CNY':
      roundedPrice = Math.round(convertedPrice * 10) / 10; // 0.1 단위로 반올림
      break;
    case 'TWD':
      roundedPrice = Math.round(convertedPrice * 2) / 2; // 0.5 단위로 반올림
      break;
    default:
      roundedPrice = Math.round(convertedPrice);
  }
  
  console.log(`[convertCurrency] 최종 변환 결과: ${price} ${sourceCurrency} -> ${roundedPrice} ${targetCurrency}`);
  return roundedPrice;
};
